//colors
$main-color: rgb(52, 80, 161);
$secondary-color: rgb(4, 25, 85);

.taskItem {
    

    display: flex;
    flex-direction: column;

    // background: $secondary-color;
    background: white;
    
    margin: 10px 0;
    width: 100%;
    border: 0 transparent;
    border-radius: 20px;

    span {
        width: 100%;
        // color: white;
        color: black;
        word-wrap: break-word;
        font-size: 18px;
    }

    

    &__row {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 0 0 0 10px;
    }

    &__date {
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 5px 0;
        background: rgb(45, 45, 45);
        color: white;
        border-bottom-left-radius: 20px;
        border-bottom-right-radius: 20px;
        font-weight: 600;

    }

    &__checked {
        text-decoration: line-through;
    }

    // input {
    //     background: $secondary-color;
    //     width: 100%;
    //     color: white;

    //     border: 1px solid white; 
    // }

    button {
        display: flex;
        justify-content: center;
        align-items: center;
        border: 0 transparent;
        border-radius: 100%;
    }

}

.taskItem-dark {
    background: rgb(30, 30, 30);

    span {
        color: white;
    }
}

